<script>
import { CChart } from '@coreui/vue-chartjs'
import { getStyle, hexToRgba } from '@coreui/utils/src'

import axios from "axios"
import vueCookies from 'vue-cookies'
import {validateSession} from '@/store/session'

export default {
  name: 'Dashboard',
  components: {
    CChart,
  },
  setup() {
    return {
      getStyle,
      hexToRgba,
    }
  },
  data: () => {
    return {

      // headerToken
      APIHOST: process.env.VUE_APP_APIS_HOST === undefined ? '' : process.env.VUE_APP_APIS_HOST,
      headers: {
        'Access-Control-Allow-Origin': '*',
        'Content-Type': 'application/json; charset=utf-8',
        'Authorization': vueCookies.get('accessToken')
      },

      currentActiveTab: 'askme',
      currentAccountTab: 'account-profile',
      tabPanePillsActiveKey: 1,

      userDetails: vueCookies.get('userDetails'),

      chatHistory: [],

      // models

    }
  }, //end of Data

  methods: {

    getRecentChats(tab) {
      let chatDetailsUrl = this.APIHOST + '/mainapp/getChatDump'
      let config = {
        method: 'get',
        maxBodyLength: Infinity,
        url: chatDetailsUrl,
        headers: {
          'Authorization': vueCookies.get('accessToken')
        }
      };
      axios.request(config)
      .then((response) => {
        for (let i = 0; i < response.data.chats.length; i++) {
          let dt = new Date(response.data.chats[i].createdTs);
          let dtHuman = dt.getFullYear() + "/" + dt.getMonth() + "/" + dt.getDate() + " - " + dt.getHours() + ":" + dt.getMinutes()
          let chat = {
            "from": "user",
            "chat": response.data.chats[i].messages.userMessage.userMessage,
            "time": dtHuman
          }
          let chatResponse = {
            "from": "bot",
            "chat": atob(response.data.chats[i].messages.output.response.text),
            "time": dtHuman
          }
          this.chatHistory.push(chat)
          this.chatHistory.push(chatResponse)
        }
        console.log(this.chatHistory)
      })
      .catch((error) => {
        console.log(error);
      });
    },

  }, // end of methods

  beforeCreate() {
    validateSession(this.$router)
  }, // end beforeCreate

  created() {
    this.getRecentChats()
  },
}
</script>
